<template>
  <div class="content">
    <van-tabs v-model="activeTabs"  :sticky="sticky" color="#FF8D59" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
      <van-tab  title="我的门店">
        <orderLeft></orderLeft>
      </van-tab>
      <van-tab title="下级代理商" >
        <orderRight></orderRight>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
  const orderLeft = () => import("../../dataStat/order/orderLeft.vue");
  const orderRight = () => import("../../dataStat/order/orderRight.vue");

  export default {
    components:{
      orderLeft,
      orderRight
    },
    data() {
      return {
        activeTabs: 0,
        sticky:true
      }
    },
    methods:{

    },
  }
</script>

<style scoped>

  .content{
    min-height: 100%;
  }
</style>



